      isImagePreview: false,
      imgUrl: "",
      sellScene_offline: false,
      sellScene_wxapp: false,
      sellScene_website: false,
      sellScene_app: false,
      isDirect: this.$route.query.isDirect,
      urlId: this.$route.query.id,
      epresentativePhotoId: '',
      epresentativePhotoId2: '',
      doorPhoto: '',
      businessLicensePhotoId: '',
      storePhoto: '',
      bankCardNumber: '',
      specialQualificationPhotoId: '',
      businessAdditionPics: '',
      specialQualificationPhotoIdZfb: '',
      goOpenAlert: false,
    };
  },
  methods: {
   
    // 选项卡切换
    onTabItemClick (index, status) {
      this.current = +index
        if( this.current === 0 ) {
          console.log(this.current)
          if (this.id) {
            this.getMerchantDetail();
          } else {
            this.$toast.error("详情数据丢失");
          }
        }else if( this.current === 1 ) {
          console.log(this.current)
          if (this.id) {
            this.getWxDetail();
          } else {
            this.$toast.error("详情数据丢失");
          }
        }else if( this.current === 2 ) {
          console.log(this.current)
          if (this.id) {
            this.getZfbDetail();
          } else {
            this.$toast.error("详情数据丢失");
          }
        }else if( this.current === 3 ) {
          if (this.id) {
            this.getTqsxfDetail();
          } else {
            this.$toast.error("详情数据丢失");
          }
        }
    },
    // 返回
    onBack() {
      this.$router.push({
        name: CLIENT_INFO
      });
    },
    // 录入资料
    onRecord(e) {
      let id = this.id.id
      if( e === 0 ) {
        this.$router.push({
          name: CLIENT_INFO_RECORD
        });
      }else if( e === 1 ) {
        clientInfoApi.wxCheck(id).then( res => {
          if( res.code === 200 ) {
            this.$router.push({
              name: SUBIT_WX
            });
          }
        })
      }else if( e === 2 ) {
        clientInfoApi.zfbCheck(id).then( res => {
          if( res.code === 200 ) {
            this.$router.push({
              name: SUBIT_ZFB
            });
          }
        })
      }else if( e === 3 ) {
        clientInfoApi.tqsxfCheck(id).then( res => {
          if( res.code === 200 ) {
            this.$router.push({
              name: SUBIT_TQ
            });
          }
        })
      }
    },
    // 获取商户详情
    getMerchantDetail() {
      let id = this.id.id
      if (id) {
        clientInfoApi.getMerchantDetail(id).then(
          res => {
            if (res.code === 200) {
              this.setDetail(res)
            } else {
              if (res && res.msg) {
                this.$toast.error(res.msg);
              }
            }
          },
          err => {
            if (err && err.msg) {
              this.$toast.error(err.msg);
            }
          }
        );
      }
    },
    // 微信进件详情
    getWxDetail() {
      let id = this.id.id
      if (id) {
        clientInfoApi.getWxDetail(id).then(
          res => {
            if (res.code === 200) {
              this.setDetail(res)
            } else {
              if (res && res.msg) {
                this.$toast.error(res.msg);
              }
            }
          },
          err => {
            if (err && err.msg) {
              this.$toast.error(err.msg);
            }
          }
        );
      }
    },
    // 获取商户详情
    getZfbDetail() {
      let id = this.id.id
      if (id) {
        clientInfoApi.getZfbDetail(id).then(
          res => {
            if (res.code === 200) {
              this.setDetail(res)
            } else {
              if (res && res.msg) {
                this.$toast.error(res.msg);
              }
            }
          },
          err => {
            if (err && err.msg) {
              this.$toast.error(err.msg);
            }
          }
        );
      }
    },
    // 获取商户详情
    getTqsxfDetail() {
      let id = this.id.id
      if (id) {
        clientInfoApi.getTqsxfDetail(id).then(
          res => {
            if (res.code === 200) {
              this.setDetail(res)
            } else {
              if (res && res.msg) {
                this.$toast.error(res.msg);
              }
            }
          },
          err => {
            if (err && err.msg) {
              this.$toast.error(err.msg);
            }
          }
        );
      }
    },
    setDetail(res) {
      this.detail = res.obj;
      this.detail.zfbRate = this.detail.zfbRate*100;
      this.detail.wxRate = this.detail.zfbRate*100;
      this.detail.tqsxfWxRate = this.detail.tqsxfWxRate*100;
      this.detail.tqsxfZfbRate = this.detail.tqsxfZfbRate*100;
      this.detail.subjectType == 'SUBJECT_TYPE_INDIVIDUAL' ? this.comTypeName = '个体工商户' : this.detail.subjectType == 'SUBJECT_TYPE_ENTERPRISE' ? this.comTypeName = '企业' : '商户类型小微';
      // 存入缓存
      for (let i in this.detail) {
        if (this.detail[i] === null) {
          this.detail[i] = "";
        }
      }
      clientInfoDetailLocal.set(JSON.stringify(this.detail));
      clientInfoDetailMerchantNameLocal.set(
        JSON.stringify({
          merchantName: this.detail.merchantName
        })
      );
      if (res.obj.sellCheck) {
        this.sellCheck = JSON.parse(res.obj.sellCheck);
      }
      this.showSell();
    },
    /**
     *显示不同售卖场景下的表单
     */
    showSell: function() {
      let select = this.sellCheck;
      let offlice = false;
      let wxapp = false;
      let website = false;
      let app = false;
      // 判断是否有选中某个属性
      for (let i in select) {
        if (select[i] === "1") {
          offlice = true;
        }
        if (select[i] === "2") {
          wxapp = true;
        }
        if (select[i] === "3") {
          website = true;
        }
        if (select[i] === "4") {
          app = true;
        }
      }
      this.sellScene_offline = offlice;
      this.sellScene_wxapp = wxapp;
      this.sellScene_website = website;
      this.sellScene_app = app;
    },
    // 获取图片
    getImage(id) {
      return id ? url + `/fms/upload/resource/thumbnail/${id}` : "";
    },
    // 预览图片
    previewImage(id) {
      // console.log(id)
      if (id) {
        this.imgUrl = id;
        this.isImagePreview = true;
      }
    }
  },

  filters: {
    // 获取图片
    loadImage(id) {
      return id ? url + `/fms/upload/resource/thumbnail/${id}` : "";
    },
    representativeTypeFil: function(val) {
      let v = parseInt(val);
      let txt = "";
      switch (v) {
        case 1:
          txt = "法人";
          break;
        case 2:
          txt = "经办人";
          break;
        default:
          break;
      }
      return txt;
    },
    // 经营类目
    businessLevFilter(businessLev) {
      if (!businessLev) {
        return "";
      }
      return "、" + businessLev;
    },
    // 销售商品场景
    sellCheckFilter(sellCheck) {
      if (!sellCheck) {
        return "";
      }
      let scenes = ["线下", "公众号/小程序", "网站", "APP"];
      sellCheck = JSON.parse(sellCheck);
      sellCheck = sellCheck.map(x => {
        return scenes[x - 1];
      });
      let tmpStr = sellCheck.join("、");
      return tmpStr;
    },
    previewLoadImage(id) {
      return id ? url + `/fms/upload/resource/${id}` : "";
    },
    // 证件类型
    certificateFilter(type) {
      return type === 1 ? "身份证" : type === 2 ? "护照" : "";
    }
  },

  mounted() {
    // this.isDirect = this.$route.query.isDirect;
    // this.id = this.$route.query.id;
     this.isDirect = this.$route.query.isDirect
    this.id = JSON.parse(clientInfoDetailIDLocal.get())
    // console.log('id 0', this.id)
    if (this.id) {
      this.getMerchantDetail();
    } else {
      this.$toast.error("详情数据丢失");
    }
  }
};
</script>
<style scoped>
  .demo-list-wrap {
    width: 98%;
    text-align: left;
    max-width: 360px;
    overflow: hidden;
    margin: 0 auto;
  }
  .wid_100{
    width: 100%;
  }
  .pad_10 {
    padding: 10px;
  }
  .ft_size {
    font-size: 14px;
  }
  .pad_20 {
    padding: 0 0.426667rem;
  }
  .hold_20 {
    height: 20px;
  }
  .hold {
    height: 2px;
  }
  .mar_bottm_10 {
    margin-bottom: 10px;
  }
  .border_bottom {
    border-bottom: 1px solid rgba(0,0,0,.12);
  }
  .bg-img-box {
    display: flex;
    justify-content: space-between;
  }
  .bg-img {
    width: 49%;
    height: 101px;
    background: #E5E5E5;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .at-img {
    width: 100%;
    height: 101px;
  }
  .result {
    border-bottom: 1px solid rgba(0,0,0,.12);
    padding: 12px 0.426667rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
</style>


